  overflow: hidden;
  position: relative;
  /* transition: .5s; */
}

.card-content {
  position: absolute;
  top: -9999px;
  left: 0;
  right: 0;
  display: flex;
  align-items: start;
  justify-content: center;
  background: rgba(0, 0, 0, 0.384);
  transition: top 0.3s linear;
  flex-direction: column;
  padding-left: 49px;
  gap: 30px;
}

