  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 33px;
  /* identical to box height */

  color: #7b5136;
}

.add-badsheed-card-price span {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
